<script>
import {mapActions} from 'vuex'
import Vue from "vue";
import {integer, maxLength, minLength, required} from "vuelidate/lib/validators";

export default {
  name: 'Login',
  data() {
    return {
      step: 1,
      email: '',
      block:'login',
      loading: false,
      showPass: false,
      showModal: false,
      account: {
        iin: '',
        password: ''
      },
    }
  },
  methods: {
    signup() {
      if (this.step == 1) {
        this.account.password = this.account.iin
        this.$http.post(API_ROOT + `/api/auth/login`, this.account)
          .then((res) => {
            this.step = 2
            localStorage.setItem("token", res.body.token);
          })
          .catch((err) => {
            Vue.toastr({
              message: 'Error',
              description: err.body.message,
              type: 'error'
            })
          })
      }
      if (this.step == 2) {
        this.$http.post(API_ROOT + `/api/user/email/set`, {'email': this.email, 'token': this.emailToken})
          .then((res) => {
            localStorage.removeItem("token")
            localStorage.removeItem("user")
            this.showModal = true
            Vue.toastr({
              message: 'Success',
              description: res.body.message,
              type: 'success'
            })


          })
          .catch((err) => {
            Vue.toastr({
              message: 'Error',
              description: err.body.message,
              type: 'error'
            })
          })
      }
    },
    signin() {
      this.loading = true
      let postData = {
        iin: this.account.iin,
        password: this.account.password,
      }

      this.$v.$touch()
      if (this.$v.account.$invalid) return

      this.$http.post(API_ROOT + `/api/auth/login`, postData)
        .then((res) => {
          localStorage.setItem('token', res.body.token)
          localStorage.setItem('user', JSON.stringify(res.body.user))
          localStorage.setItem('has_email', res.body.has_email)
          if (res.body.has_email == false) {
            this.block = 'activate'
            this.step = 2
          } else {
            if (this.$route.query.redirect) {
              this.$router.push(this.$route.query.redirect)
            } else {
              this.$router.push({name: 'profile'})
            }

            if (!res.body.user.coins) {
              localStorage.setItem('coins', 0)
            } else {
              localStorage.setItem('coins', res.body.user.coins.active_coins)
            }

            this.$router.push({name: 'profile'})
          }

          this.loading = false;
          // this.$router.push({ name: 'profile' })
        })
        .catch((err) => {

          if (err.status === 422 && err.body.need_verification === true) {
            this.showModal = true
          }

          this.loading = false;
          console.log(err)
        })
    },
  },

  validations: {
    account: {
      iin: {
        required,
        minLength: minLength(12),
        maxLength: maxLength(12),
        integer,
      },
      password: {
        required,
        minLength: minLength(6),
      }
    },
  }
}
</script>
